@font-face {
  font-family: 'Jost';
  src:  url('../src/assets/reg.woff') format('woff2'),
        url('../src/assets/reg.woff') format('woff');
}
body {
  margin: 0;
  font-family: 'Jost', Fallback, sans-serif;
  -webkit-font-smoothing: antialia<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">sed;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  /* background-size: 100% 80%; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.holder {
  background-color: grey;
  height: 20px;
}

button:focus {outline:0;}

button:hover {
  cursor: pointer
}

.hide {
  display:none !important;
}

.position--fixed {
  position: fixed;
}

.error {
  color: red;
}
.error-border {
  border: solid 1px red;
}
